body {
  background-color: #2a2a2e;
  font-size: 2rem;
}

#gameContainer {
  background: #737373;
  border: 2px solid #000;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  display: flex;
}

#inputText {
  width: 15rem;
  text-align: center;
  border: 2px solid #000;
  border-radius: 4px;
  margin: 1rem;
  font-size: 1.5rem;
}

.newGameDiv {
  flex-direction: column;
  display: flex;
}

.newGameDiv > h1 {
  font-size: 3rem;
}

.newGameDiv > button {
  background-color: #90ee90;
  border: 2px solid #000;
  border-radius: 4px;
  font-size: 1.5rem;
}

.selections {
  justify-content: center;
  margin: 1rem;
  display: flex;
}

.selection {
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
  padding: 1rem;
  font-size: 3rem;
  transition: all .1s;
}

.selection:hover {
  transform: scale(1.2);
}

.hidden {
  display: none;
}

#results {
  grid-template-columns: repeat(2, 1fr);
  justify-content: center;
  place-items: center;
  gap: 1rem;
  margin: 1.5rem auto;
  display: grid;
}

#scoreDiv {
  flex-direction: row;
  align-items: center;
  display: flex;
}

.resultScore {
  color: #fff;
  background-color: #2a2a2e;
  border: 2px solid #000;
  border-radius: 5px;
  margin-left: .5rem;
  padding: .5rem;
}

.player-score {
  color: #fff;
  background-color: #2a2a2e;
  border-radius: 4px;
  margin: .5rem;
  padding: .5rem;
  font-size: 2rem;
}

.resultSelection {
  opacity: .5;
}

.resultSelection.winner {
  opacity: 1;
  font-size: 2.5rem;
}

#displayDiv {
  border: 2px solid #000;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  margin-top: 1rem;
  display: flex;
}

